@use "../../styles/_variables.scss" as *;
@use "../../styles/_reset.scss" as *;
@use "../../styles/_typography.scss" as *;

.white-button{
    position: relative;
    height: 2.5rem;
    width: 5rem;
    border-radius: 0.5rem;
    border: 0.125rem solid $color-black;
    background-color: $color-white;
    color: $color-black;
    font-family: $primary-font-family;
    font-weight: 900;
    font-size: 0.95rem;
    box-shadow: 0.1rem 0.15rem 0.25rem $color-black;
    transition: 300ms;
    &:hover{
        cursor: pointer;
        height: 2.75rem;
        width: 5.5rem;
        box-shadow: 0.05rem 0.075rem 0.25rem $color-black;
        text-decoration: underline;
    }
    &:active{
        cursor: pointer;
        text-decoration: underline;
        height: 2.5rem;
        width: 5rem;
        box-shadow: 0.1rem 0.15rem 0.25rem $color-black;
        transition: 50ms;
    }
}